          </div>
          <div class="text">
            <p>
              <span>{{ loginUser.nickname }}</span>
            </p>
            <p>绑定手机号:&nbsp;{{ loginUser.phone }} &nbsp;</p>
          </div>
        </div>
        <div class="currentBnt">当前账号</div>
      </div>
    </div>
    <div class="list">
      <div class="item">
        <div>昵称</div>
        <input class="text" v-model="loginUser.nickname"/>
      </div>
      <div class="item">
        <div>ID号</div>
        <div class="text">{{ loginUser.uid }}<van-icon name="lock" /></div>
      </div>
      <div class="item">
        <div>手机号码</div>
        <div class="text">{{ loginUser.phone }}<van-icon name="lock" /></div>
      </div>
      <div class="item">
        <div>密码</div>
        <router-link class="text" to="/changepassword" tag="div">点击修改密码<van-icon name="arrow" /></router-link>
      </div>
    </div>
    <van-button round class="signout" type="info" @click="logOut"
      >退出登录</van-button
    >
  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      loginUser: null,
    };
  },
  created() {
    this.$store.getters.loginUser;
    this.loginUser = this.$store.getters.loginUser.data;
  },
  methods: {
    logOut() {
      this.loginOut();
      this.$toast({
        message: "退出成功!",
        onClose: () => {
          this.$router.push("/login");
        },
      });
    },
    ...mapMutations(["saveToken", "saveLoginInfo", "loginOut"]),
  },
  computed: {
    ...mapGetters(["loginUser"]),
  },
};
</script>
